.chat{
    display: flex;
    flex-direction: column;
    flex: 0.65;
}

.chat_header{
    display: flex;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid lightgray;
}
.chat_headerInfo{
    flex: 1;
padding-left: 20px;
}
.chat_headerInfo > h3{
    font-weight: 500;
    margin-bottom: 3px;
}
.chat_headerInfo > p{
    color: gray;
}
.chat_body{
    flex: 1;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;
}
.chat_messages{
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    border-radius: 10px;
    background-color: #ffff;
    margin-bottom: 30px;
}
.chat_timestamp{
    margin-left: 10px;
    font-size: xx-small;
}
.chat_name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}
.chat_reciever{
    margin-left: auto;
    background-color: #dcf8c6;
}
.chat_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}
.chat_footer>form{
    flex: 1;
    display: flex;
    /* outline-width: 0;/ */
}
.chat_footer>form>input{
    flex: 1;
    border-radius: 10px;
    border: none;
    outline-width: 0;
    height: 30px;
}
.chat_footer>form>button{
    display: none;
}
.chat_footer>.MuiSvgIcon-root{
 padding: 10px;
 color: gray;
}